@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@200&family=Poppins:wght@200&display=swap');
body {
  margin: 0;
  font-family: 'Kanit', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #273c75;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
div h3{
  color: azure;
}
.subject{
  background-color: #d6dde7;
  border: 1px solid rgb(47, 47, 247);
  padding: 10px;
  border-radius: 20px;
  box-shadow: 20px 20px 50px 10px #54a0ff inset;
}
.inputBtn{
  height: 25px;
  color: rgb(1, 1, 19);
  margin: 5px 0px;
  text-align: center;
  border-radius: 10px;
  font-size: 17px;
  font-weight: bold;
}

.btn{
  height: 30px;
  width: 150px;
  background-color: rgb(47, 47, 247);
  color: white;
  border: none;
  font-weight: bold;
  font-size: medium;
  margin-top: 5px;
}
.result{
  width: 20%;
}

#scoreboard{
  background-color: white;
  height: auto;
  display: none;
}
#scoreboard h3{
  color: #273c75;
  padding-top: 10px;
  font-size: 22px;
  font-style: italic;
  text-decoration: underline;
}
.name{
  font-size: 24px;
  font-weight: bold;
  font-style: italic;
  text-decoration: underline;
  
}
.grade{
  width: 30px;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid black;
}